.header {
    padding: 10px;
    will-change: transform;
    transform: translate(-90%, 0);
    max-width: max-content;
    background: #FFFFFF;
    box-shadow: 0px 0px 40px rgba(29, 33, 38, 0.13), 0px 0px 2px rgba(29, 33, 38, 0.1);
    border-radius: 10px;
    margin: 10px;
    transition: transform 0.3s ease-in-out 0s;

}

.header:hover {
    transform: translate(0);
}

.header__nav {
    display: flex;
    gap: 10px;

}

.header__link {
    text-decoration: none;
    color: black;
    transition: color 0.3s ease-in-out 0s;
    will-change: color;
}

.header__link.active {
    color: orange;
    text-decoration: underline;
}

