.edit {
    padding: 15px;
}

.edit__header {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    gap: 5px;
}

.edit__img {
    width: 20px;
    height: 20px;
    object-fit: cover;
}

.edit__text {
    cursor: pointer;
}

.edit__btns {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.edit__btn {
    cursor: pointer;
    padding: 10px 20px;
    background: lightskyblue;
    border-radius: 4px;
    border: 1px solid transparent;

    will-change: background, color, border-color;
    transition: background 0.3s ease 0s, color 0.3s ease 0s, border-color 0.3s ease 0s;
}

.edit__btn:hover {
    background: transparent;
    color: lightskyblue;
    border-color: lightskyblue;
}